<template>
<div class="panel">
    
    <div class="page__bd">

        <div v-for="item in meetingRaterList" class="weui-panel">
             <div class="weui-panel__hd">No.{{item.meetno}} {{getPrompt(item)}}</div>
            <div class="weui-panel__bd">
                <router-link :to="getUrl(item)" class="weui-media-box weui-media-box_appmsg">
                    <div class="weui-media-box weui-media-box_text">
                        <h4 class="weui-media-box__title">{{item.bu}} - {{item.meetingTitle}}</h4>
                        <h4 class="weui-media-box__title" style="color:green;"> {{rateLevelTag(item)}} </h4>
                        <br>
                        <ul class="weui-media-box__info" style="margin-top: 0px;">
                            <li class="weui-media-box__info__meta">评价进度</li>
                            <li class="weui-media-box__info__meta">{{item.ratedNum}} / {{item.totalNum}}</li>
                        </ul>
                    </div>
                </router-link>
            </div>

            <div class="weui-panel__hd_bar">
                <mt-progress :value="(item.ratedNum/item.totalNum)*100" :bar-height="5">
                </mt-progress>
            </div>
        </div>

    </div>
</div>
</template>

<script>
import server from '../assets/js/server.js'


export default {

    name: 'rater-list',
    data() {
        return {
            badge: this.$store.state.user.badge,
            meetingRaterList: [],
            value: '',
            search: 'false'
        };
    },
    mounted() {

        var this2 = this;

        server.meetingList(this.badge).then(function(response) {

            //console.log(response.data);

            this2.meetingRaterList = response.data;

        }).catch(function(error) {

            console.log(error);
        
        });
    },

    methods: {

        rateLevelTag:function(item){

            var level = item.ratelevel;

            if(1==level && item.meetingType == '1') return "系总轮次";
            if(1==level && item.meetingType == '0') return "海选轮次";
            if(2==level && item.meetingType == '1') return "事总轮次";
            if(2==level && item.meetingType == '0') return "优中选优轮";
            if(3==level) return "优中选优轮";
        },

        getPrompt: function(item) {
            var s = '';
            if (item.meetingType == '1')
                s = '两大课件心得';
            else
                s = '会议心得';
            return s;
        },

        getUrl: function(item) {
            return '/ratermain?meetingId=' + item.meetingId +'&ratelevel='+item.ratelevel;
        }
    },
    computed: {

    }
};
</script>

<style scoped>

.weui-media-box__title{
  white-space: normal;
}

.weui-media-box {
    padding: 10px 10px 5px 10px;
}

.weui-panel__hd_bar {
    padding: 0px 15px 5px 20px;
    color: #999999;
    font-size: 13px;
    position: relative;
}

.sss {
    width: 110px;
}


.c {
    color: black;
}


.page-tabbar {
    overflow: hidden;
    height: 100vh;
}

.page-wrap {
    overflow: auto;
    height: 100%;
    padding-bottom: 100px;
}

.weui-tabbar__icon {
    display: inline-block;
    width: 27px;
    height: 27px;
}

.expireDay1 {
    display: inline-block;
    padding: .25em .4em;
    min-width: 8px;
    border-radius: 18px;
    color: black;
    line-height: 1.2;
    text-align: center;
    font-size: 15px;
    vertical-align: middle;
    margin-left: 30px;
}

.weui-tabbar__label {
    text-align: center;
    color: green;
    font-size: 12px;
    line-height: 1.8;
}

.weui-tabbar__item {
    background-color: #F2F4F3;
}
</style>



