import axios from 'axios'

var server_pre = 'https://open.auxgroup.com/api/auxhr/meetingrate/';
//var server_pre = 'http://localhost:9999/'

const meetingAddress = server_pre + 'meetings/';
const ServerAddress = server_pre + 'raters/';
const participantsServer = server_pre + 'participants/';

export default {

  //used

  //评价会议列表
  //RaterHome.vue
  meetingList(badge) {
    return axios.get(ServerAddress+"meetingList/"+badge)
  },

  // RaterMain.vue
  meetingRaterListByLevel(meetingId,badge,level) {//会议评价列表
    return axios.get(ServerAddress+"meetingRaterList/"+meetingId+"/"+badge+"/"+level)
  },

  raterTypes(raterId){
    return axios.get(ServerAddress + raterId + "/types")
  },


  //没确定
  
  rater(id) {
    return axios.get(ServerAddress + id)
  },

  meetingType(meetingId){
    return axios.get(meetingAddress + meetingId + "/types")
  },

  meetingTypeByRound(meetingId , round){
    return axios.get(meetingAddress + meetingId + "/types/" +round)
  },

  meetingTypebyPartiId(partiId){
    return axios.get(meetingAddress + "partiId/" +partiId+ "/types")
  },

  ratercommit(rater) {
  	return axios.post(ServerAddress + "commit/" , rater)
  },
  ratersave(rater) {
  	return axios.post(ServerAddress + "save/" , rater)
  },

  ratersavefinal(ratertypes) {
    return axios.post(ServerAddress + "savefinal/" , ratertypes)
  },

  ratercommitfinal(ratertypes , partiId) {
    return axios.post(ServerAddress + "commitfinal/"+partiId , ratertypes)
  },


  raterlist(badge) {
  	return axios.get(ServerAddress + "list/" + badge)
  },
  singleRaterList(raterId) {
    return axios.get(ServerAddress+"singleRaterList/"+raterId)
  },

  meetingRaterList(meetingId,badge) {//会议评价列表
    return axios.get(ServerAddress+"meetingRaterList/"+meetingId+"/"+badge)
  },

  raterNumber(badge) {
  	return axios.get(ServerAddress + 'number/' + badge)
  },

  participantsData(partid){
    return axios.get(participantsServer + partid)
  },

  participantNumber(badge) {
  	return axios.get(participantsServer + 'number/' + badge)
  },
  participantsView(id) {
    return axios.get(participantsServer + 'view/' + id)
  },
  participantsViewDetail(id) {
    return axios.get(participantsServer + 'thinkingdetail/' + id)
  }

}



